@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.custom-button {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  padding: 10px 20px;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--white-color);
  border: none;
  cursor: pointer;
}

.custom-button:disabled {
  background: var(--white-color);
  border: 1px solid var(--primary-color);
  color: var(--dark-color);
}

.custom-button:hover .custom-button:disabled {
  background-color: transparent;
}

.button-dark,
.button-primary {
  transition: background 0.4s ease;
}

.button-primary {
  background-color: var(--primary-color);
}
.button-primary:hover {
  background-color: var(--dark-color);
}

.button-dark {
  background-color: var(--dark-color);
}
.button-dark:hover {
  background-color: var(--primary-color);
}

.custom-button:not(:last-child) {
  margin: 20px 10px 20px 0;
}

.gallery__item {
  position: relative;
  height: 450px;
  width: 50%;
  overflow: hidden;
}

.gallery__item-remove {
  position: absolute;
  right: 0;
  top: 0;
}

.gallery__item:hover .gallery__image {
  transform: scale(1.2);
}

.gallery__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 4s linear;
}

@media (max-width: 768px) {
  .gallery__item {
    width: 100%;
  }
}

.gallery {
  display: flex;
  flex-wrap: wrap;
}

.pagination {
  margin-right: 20px;
  /* display: flex;
  justify-content: center;
  align-items: center; */
}

.pagination__buttons {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pagination__current-page {
  letter-spacing: 1.4px;
  margin: 0 10px;
}

@media (max-width: 500px) {
  .pagination {
    width: 100%;
    margin-right: 0;
  }
  .pagination .custom-button {
    margin-right: 0;
  }
  .pagination__buttons {
    flex-direction: column;
    margin-bottom: 20px;
  }
  .pagination__current-page {
    order: -1;
  }
}

.filters {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 20px 0;
}

.limit-text {
  margin-right: 15px;
}

.limit {
  padding: 10px 20px;
  max-height: 20px;
}

@media (max-width: 500px) {
  .filters .custom-button {
    width: 100%;
  }
}

.preloader-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 40px 0;
}

.preloader {
  display: inline-block;
  width: 60px;
  height: 60px;
}
.preloader:after {
  content: " ";
  display: block;
  width: 40px;
  height: 40px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #fff;
  border-color: var(--primary-color) transparent var(--dark-color) transparent;
  animation: preloading 1.2s linear infinite;
}
@keyframes preloading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.photo-page__image {
  max-width: 100%;
}

.photo-page__content {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
}

.photo-page__size {
  display: flex;
  justify-content: center;
  align-items: center;
}

.photo-page__size-item,
.photo-page__author {
  margin: 10px;
}

@media (max-width: 600px) {
  .photo-page__content {
    flex-direction: column;
  }
}

.header {
  padding: 20px 0;
  background-color: var(--dark-color);
  margin-bottom: 20px;
}
.header__link {
  color: var(--white-color);
  text-transform: capitalize;
}
.header__link:not(:last-child) {
  margin-right: 15px;
}

body {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
}

:root {
  --primary-color: #e8503e;
  --dark-color: #2e3442;
  --white-color: #ffffff;
}

a {
  text-decoration: none;
}

.container {
  width: 90%;
  max-width: 1300px;
  margin: 0 auto;
}

.info-message {
  margin: 20px 0;
  padding: 15px;
  text-align: center;
  color: #2e3442;
  color: var(--dark-color);
  border: none;
}

.page-title {
  text-align: center;
  font-size: 40px;
  color: #2e3442;
  color: var(--dark-color);
  margin-bottom: 20px;
}

